import React, { useState, useEffect } from 'react';
import { ArrowRight, Play, CheckCircle, Shield, Award, ChevronRight, X } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

const VideoModal = ({ isOpen, onClose }) => {
  return (
    <AnimatePresence>
      {isOpen && (
        <motion.div 
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          className="fixed inset-0 z-50 flex items-center justify-center p-4"
        >
          {/* Overlay con blur */}
          <motion.div 
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="absolute inset-0 bg-black/80 backdrop-blur-sm"
            onClick={onClose}
          />
          
          {/* Container Video */}
          <motion.div 
            initial={{ scale: 0.9, y: 20 }}
            animate={{ 
              scale: 1, 
              y: 0,
              transition: {
                type: "spring",
                damping: 25,
                stiffness: 300
              }
            }}
            exit={{ 
              scale: 0.9, 
              y: 20,
              opacity: 0,
              transition: {
                duration: 0.2
              }
            }}
            className="relative w-full max-w-4xl bg-slate-900 rounded-2xl overflow-hidden shadow-2xl"
          >
            {/* Pulsante chiusura */}
            <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.95 }}
              onClick={onClose}
              className="absolute top-4 right-4 z-10 w-10 h-10 bg-white/10 hover:bg-white/20 rounded-full flex items-center justify-center text-white"
            >
              <X size={20} />
            </motion.button>

            {/* Video Player */}
            <div className="relative pt-[56.25%]">
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ 
                  opacity: 1,
                  transition: { delay: 0.2 }
                }}
                className="absolute inset-0"
              >
                <video
                  className="absolute inset-0 w-full h-full object-cover"
                  autoPlay
                  controls
                >
                  <source src="/assets/vipra_video.mp4" type="video/mp4" />
                  Your browser does not support the video tag.
                </video>
              </motion.div>
            </div>
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
};

const ProfessionalHero = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isVideoOpen, setIsVideoOpen] = useState(false);

  const dynamicTexts = [
    { text: 'Security', color: 'from-blue-400 to-cyan-400' },
    { text: 'Technology', color: 'from-purple-400 to-blue-400' },
    { text: 'AI', color: 'from-cyan-400 to-indigo-400' },
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % dynamicTexts.length);
    }, 2500);
    return () => clearInterval(interval);
  }, []);

  // Handler per aprire/chiudere il video
  const handleVideoToggle = () => setIsVideoOpen(!isVideoOpen);

  // Previeni lo scroll quando il modale è aperto
  useEffect(() => {
    if (isVideoOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isVideoOpen]);

  return (
    <section className="min-h-[100dvh] w-full relative overflow-auto bg-gradient-to-br from-slate-900 via-blue-900/20 to-slate-900 flex items-center justify-center px-2 md:px-0 py-8 md:py-0">
      {/* Particle background */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        {[...Array(100)].map((_, i) => (
          <div
            key={i}
            className="absolute rounded-full bg-blue-500/10 animate-float"
            style={{
              width: `${Math.random() * 8 + 2}px`,
              height: `${Math.random() * 8 + 2}px`,
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              animationDelay: `${i * 0.1}s`,
              animationDuration: `${5 + Math.random() * 15}s`,
            }}
          />
        ))}
      </div>

      {/* Glow effects */}
      <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-blue-500/10 rounded-full blur-[100px] hidden md:block" />
      <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-cyan-500/10 rounded-full blur-[100px] hidden md:block" />

      <div className="container mx-auto px-2 md:px-6 h-full flex flex-col justify-center relative z-10">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 md:gap-12 items-center">
          <div className="text-center lg:text-left py-8 md:py-0">
            <div className="inline-block mb-6 md:mb-8 px-4 md:px-6 py-2 md:py-3 bg-blue-500/10 border border-blue-500/30 rounded-full backdrop-blur-sm">
              <span className="text-blue-400 font-medium text-base md:text-lg">Be aware of what you can't see</span>
            </div>

            <h1 className="text-3xl xs:text-4xl sm:text-5xl md:text-7xl lg:text-8xl font-bold text-white mb-4 md:mb-6 leading-tight">
              <span className="bg-gradient-to-r from-white via-blue-100 to-cyan-100 bg-clip-text text-transparent">
                VIPRA
              </span>
              <br />
              <div className="h-16 md:h-24 overflow-visible relative mb-6 md:mb-10 pb-2 md:pb-4">
                {dynamicTexts.map((item, index) => (
                  <div
                    key={index}
                    className={`absolute inset-0 transition-all duration-500 ${index === currentIndex
                        ? 'opacity-100 translate-y-0'
                        : 'opacity-0 translate-y-8'
                      }`}
                  >
                    <span className={`bg-gradient-to-r ${item.color} bg-clip-text text-transparent text-xl xs:text-2xl sm:text-3xl md:text-5xl lg:text-6xl font-bold`}>
                      {item.text}
                    </span>
                  </div>
                ))}
              </div>
            </h1>

            <p className="text-base xs:text-lg md:text-2xl text-slate-300 mb-6 md:mb-10 max-w-2xl mx-auto lg:mx-0 leading-relaxed">
              Veicoli Interconnessi per la Prevenzione dei Rischi su Asfalto
            </p>

            <div className="flex flex-col sm:flex-row gap-4 justify-center lg:justify-start">
              <button
                onClick={() => window.dispatchEvent(new CustomEvent('scrollToSection', { detail: 'about' }))}
                className="group relative bg-gradient-to-r from-blue-600 to-cyan-600 hover:from-blue-700 hover:to-cyan-700 text-white px-6 md:px-8 py-3 md:py-4 rounded-xl font-semibold text-base md:text-lg transition-all duration-300 transform hover:scale-105 shadow-xl overflow-hidden"
              >
                <span className="relative z-10 flex items-center gap-3">
                  More about VIPRA
                  <ArrowRight className="group-hover:translate-x-1 transition-transform duration-300" size={20} />
                </span>
                <div className="absolute inset-0 bg-gradient-to-r from-white/0 via-white/20 to-white/0 translate-x-[-100%] group-hover:translate-x-[100%] transition-transform duration-700" />
              </button>
              <motion.button 
                onClick={handleVideoToggle}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.98 }}
                className="group border-2 border-blue-500/50 hover:border-blue-500 text-blue-400 hover:text-white px-6 md:px-8 py-3 md:py-4 rounded-xl font-semibold text-base md:text-lg backdrop-blur-sm flex items-center gap-3 hover:bg-blue-500/20"
              >
                <motion.div
                  whileHover={{ rotate: 360 }}
                  transition={{ duration: 0.5 }}
                >
                  <Play size={20} />
                </motion.div>
                Watch Video
              </motion.button>
            </div>
          </div>

          <div className="relative hidden lg:block">
            <div className="relative w-full h-72 md:h-96">
              {/* 3D Card Effect */}
              <div className="absolute w-full h-full bg-gradient-to-br from-blue-500/10 to-cyan-500/10 rounded-3xl border border-blue-500/30 backdrop-blur-lg shadow-2xl transform rotate-3 hover:rotate-0 transition-all duration-500">
                <div className="absolute inset-0">
                  <img 
                    src="/assets/imgHero.jpg"
                    alt="VIPRA Logo"
                    className="w-full h-full object-cover rounded-3xl"
                    style={{ filter: 'drop-shadow(0 0 10px rgba(56, 189, 248, 0.5))' }}
                  />
                </div>
              </div>

              {/* Floating elements */}
              <div className="absolute -top-8 -left-8 w-32 h-32 bg-gradient-to-br from-blue-500/20 to-cyan-500/20 rounded-full blur-xl" />
              <div className="absolute -bottom-8 -right-8 w-40 h-40 bg-gradient-to-br from-indigo-500/20 to-blue-500/20 rounded-full blur-xl" />
            </div>
          </div>
        </div>

        {/* Trust badges */}
        <div className="mt-10 md:mt-20 grid grid-cols-2 md:grid-cols-3 gap-4 md:gap-6 max-w-5xl mx-auto">
          {[
            { icon: <CheckCircle size={24} />, text: 'Local AI', highlight: 'Limited Resources, State of the art results' },
            { icon: <Award size={24} />, text: 'Low Power', highlight: '5V, 5A' },
            { icon: <Shield size={24} />, text: 'Security', highlight: 'Be aware of what you can not see' },
          ].map((badge, index) => (
            <div
              key={index}
              className="bg-slate-800/30 backdrop-blur-sm border border-slate-700/50 p-4 md:p-6 rounded-xl hover:border-blue-500/50 transition-all duration-300 transform hover:scale-[1.02] hover:bg-slate-800/50"
            >
              <div className="flex items-center gap-4">
                <div className="w-10 h-10 md:w-12 md:h-12 bg-gradient-to-br from-blue-500/20 to-cyan-500/20 rounded-lg flex items-center justify-center text-blue-400">
                  {badge.icon}
                </div>
                <div>
                  <div className="text-white font-medium text-sm md:text-base">{badge.text}</div>
                  <div className="text-blue-400 font-bold text-xs md:text-base">{badge.highlight}</div>
                </div>
              </div>
            </div>
          ))}
        </div>

      </div>

      {/* Aggiungi il componente VideoModal */}
      <VideoModal 
        isOpen={isVideoOpen}
        onClose={() => setIsVideoOpen(false)}
      />
    </section>
  );
};

export default ProfessionalHero;