import React, { useState, useEffect } from 'react';
import { Eye, Globe, Cpu, Database, Shield, Award, Rocket, Users, Layers, TrendingUp, BarChart3 } from 'lucide-react';

const ProfessionalAbout = () => {
  // Rimuovi useScrollAnimation e usa un semplice stato per la visibilità
  const [isVisible, setIsVisible] = useState(false);
  const [activeYear, setActiveYear] = useState(2024);

  // Rendi visibile il componente immediatamente
  useEffect(() => {
    const timer = setTimeout(() => setIsVisible(true), 100);
    return () => clearTimeout(timer);
  }, []);

  const milestones = [
    {
      year: 2024,
      title: "Prototype Development",
      description: "First working prototype using lightweight hardware: Raspberry Pi 5, ESP32 with LoRa module, ESP32 PoE, and Raspberry Pi Camera. Focus on distributed road hazard detection and alerting.",
      icon: <Cpu className="w-6 h-6" />,
      color: "bg-cyan-500"
    },
    {
      year: 2023,
      title: "Concept & Architecture",
      description: "System design for distributed road safety alerts. Emphasis on affordable, low-power devices and open communication between vehicles.",
      icon: <Globe className="w-6 h-6" />,
      color: "bg-blue-500"
    }
  ];

  return (
    <section id="about" className="min-h-screen py-24 relative overflow-hidden flex items-center">
      <div className="absolute inset-0 bg-gradient-to-b from-slate-900/50 to-transparent" />
      
      <div className="container mx-auto px-6 relative z-10 w-full">
        <div className={`text-center mb-20 transition-all duration-700 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-5'}`}>
          <div className="inline-flex items-center gap-2 bg-indigo-500/10 border border-indigo-500/30 px-4 py-2 rounded-full text-indigo-400 text-sm font-medium mb-6">
            <Eye size={16} />
            Project Overview
          </div>
          <h2 className="text-4xl md:text-6xl font-bold text-white mb-8 leading-tight">
            Distributed
            <span className="block bg-gradient-to-r from-indigo-400 to-blue-400 bg-clip-text text-transparent">
              Road Safety Prototype
            </span>
          </h2>
          <p className="text-xl text-slate-300 max-w-3xl mx-auto leading-relaxed">
            We are building a distributed road hazard alert system using lightweight, affordable hardware such as Raspberry Pi 5, ESP32 LoRa, ESP32 PoE, and Raspberry Pi Camera. The system is not instantaneous, but is designed for real-world feasibility and low power consumption. Currently, this is a prototype and we do not have clients yet.
          </p>
          
          {/* Hardware showcase */}
          <div className="flex justify-center gap-6 mt-10 mb-4 flex-wrap">
            <div className="bg-slate-800/50 p-4 rounded-lg border border-slate-700/50">
              <div className="w-24 h-16 bg-gradient-to-br from-green-500 to-emerald-600 rounded flex items-center justify-center mb-2">
                <Cpu className="w-8 h-8 text-white" />
              </div>
              <span className="text-xs text-slate-400">Raspberry Pi 5</span>
            </div>
            <div className="bg-slate-800/50 p-4 rounded-lg border border-slate-700/50">
              <div className="w-24 h-16 bg-gradient-to-br from-blue-500 to-cyan-600 rounded flex items-center justify-center mb-2">
                <Database className="w-8 h-8 text-white" />
              </div>
              <span className="text-xs text-slate-400">ESP32 LoRa</span>
            </div>
            <div className="bg-slate-800/50 p-4 rounded-lg border border-slate-700/50">
              <div className="w-24 h-16 bg-gradient-to-br from-purple-500 to-violet-600 rounded flex items-center justify-center mb-2">
                <Shield className="w-8 h-8 text-white" />
              </div>
              <span className="text-xs text-slate-400">ESP32 PoE</span>
            </div>
            <div className="bg-slate-800/50 p-4 rounded-lg border border-slate-700/50">
              <div className="w-24 h-16 bg-gradient-to-br from-orange-500 to-red-600 rounded flex items-center justify-center mb-2">
                <Eye className="w-8 h-8 text-white" />
              </div>
              <span className="text-xs text-slate-400">Pi Camera</span>
            </div>
          </div>
        </div>

        <div className="flex flex-col lg:flex-row gap-12">
          <div className="lg:w-1/3">
            <div className="sticky top-32">
              <div className="relative">
                <div className="absolute -left-1 top-0 bottom-0 w-1 bg-slate-700/50 rounded-full" />
                
                {milestones.map((milestone, index) => (
                  <button
                    key={milestone.year}
                    onClick={() => setActiveYear(milestone.year)}
                    className={`relative flex items-center gap-4 w-full p-4 rounded-lg transition-all duration-300 ${
                      activeYear === milestone.year 
                        ? 'bg-slate-800/50 backdrop-blur-sm border border-slate-700/50 shadow-lg' 
                        : 'hover:bg-slate-800/30'
                    }`}
                    style={{ marginBottom: index === milestones.length - 1 ? 0 : '1rem' }}
                  >
                    <div className={`w-10 h-10 rounded-full flex items-center justify-center ${milestone.color} text-white shadow-md`}>
                      {milestone.icon}
                    </div>
                    <div className="text-left">
                      <div className={`font-bold transition-colors duration-300 ${
                        activeYear === milestone.year ? 'text-white' : 'text-slate-300'
                      }`}>
                        {milestone.year}
                      </div>
                      <div className={`text-sm transition-colors duration-300 ${
                        activeYear === milestone.year ? 'text-blue-300' : 'text-slate-400'
                      }`}>
                        {milestone.title}
                      </div>
                    </div>
                    <div className={`absolute -left-1 top-1/2 transform -translate-y-1/2 w-3 h-3 rounded-full transition-all duration-300 ${
                      activeYear === milestone.year ? 'bg-blue-400 scale-150' : 'bg-slate-500'
                    }`} />
                  </button>
                ))}
              </div>
            </div>
          </div>

          <div className="lg:w-2/3">
            {milestones.filter(m => m.year === activeYear).map(milestone => (
              <div 
                key={milestone.year} 
                className={`bg-gradient-to-br from-slate-800/50 to-slate-900/50 backdrop-blur-sm border border-slate-700/50 rounded-xl p-8 transition-all duration-500 ${
                  isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-5'
                }`}
              >
                <div className="flex items-center gap-4 mb-6">
                  <div className={`w-16 h-16 rounded-xl flex items-center justify-center ${milestone.color} text-white shadow-lg`}>
                    {milestone.icon}
                  </div>
                  <div>
                    <h3 className="text-2xl font-bold text-white">{milestone.title}</h3>
                    <div className="text-blue-400 font-medium">{milestone.year}</div>
                  </div>
                </div>
                
                <p className="text-slate-300 leading-relaxed mb-8">
                  {milestone.description}
                </p>

                {/* Caratteristiche tecniche */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {[
                    { label: "Hardware Type", value: "Prototype", icon: <Layers size={16} /> },
                    { label: "Communication", value: "LoRa", icon: <Database size={16} /> },
                    { label: "Power Mode", value: "Low Power", icon: <TrendingUp size={16} /> },
                    { label: "Status", value: "Research", icon: <BarChart3 size={16} /> }
                  ].map((stat, index) => (
                    <div key={index} className="bg-slate-800/30 border border-slate-700/50 rounded-lg p-4 flex items-center gap-4">
                      <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${milestone.color}/20 text-blue-400`}>
                        {stat.icon}
                      </div>
                      <div>
                        <div className="text-sm text-slate-400">{stat.label}</div>
                        <div className="text-xl font-bold text-white">{stat.value}</div>
                      </div>
                    </div>
                  ))}
                </div>

                {/* Dettagli aggiuntivi per ogni anno */}
                {milestone.year === 2024 && (
                  <div className="mt-8 p-6 bg-gradient-to-r from-cyan-500/10 to-blue-500/10 border border-cyan-500/30 rounded-lg">
                    <h4 className="text-lg font-semibold text-cyan-400 mb-3">Current Development</h4>
                    <ul className="space-y-2 text-slate-300">
                      <li className="flex items-center gap-2">
                        <div className="w-2 h-2 bg-cyan-400 rounded-full"></div>
                        Distributed hazard detection using computer vision
                      </li>
                      <li className="flex items-center gap-2">
                        <div className="w-2 h-2 bg-cyan-400 rounded-full"></div>
                        LoRa-based vehicle-to-vehicle communication
                      </li>
                      <li className="flex items-center gap-2">
                        <div className="w-2 h-2 bg-cyan-400 rounded-full"></div>
                        Real-time data logging and analysis
                      </li>
                      <li className="flex items-center gap-2">
                        <div className="w-2 h-2 bg-cyan-400 rounded-full"></div>
                        Energy-efficient design for practical deployment
                      </li>
                    </ul>
                  </div>
                )}

                {milestone.year === 2023 && (
                  <div className="mt-8 p-6 bg-gradient-to-r from-blue-500/10 to-indigo-500/10 border border-blue-500/30 rounded-lg">
                    <h4 className="text-lg font-semibold text-blue-400 mb-3">Foundation Work</h4>
                    <ul className="space-y-2 text-slate-300">
                      <li className="flex items-center gap-2">
                        <div className="w-2 h-2 bg-blue-400 rounded-full"></div>
                        System architecture design and planning
                      </li>
                      <li className="flex items-center gap-2">
                        <div className="w-2 h-2 bg-blue-400 rounded-full"></div>
                        Hardware selection and compatibility testing
                      </li>
                      <li className="flex items-center gap-2">
                        <div className="w-2 h-2 bg-blue-400 rounded-full"></div>
                        Communication protocol definition
                      </li>
                      <li className="flex items-center gap-2">
                        <div className="w-2 h-2 bg-blue-400 rounded-full"></div>
                        Initial feasibility studies
                      </li>
                    </ul>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default ProfessionalAbout;