import React, { useState, useEffect, useRef } from 'react';
import ProfessionalParticleSystem from './components/ProfessionalParticleSystem';
import ProfessionalLoader from './components/ProfessionalLoader';
import ProfessionalHeader from './components/ProfessionalHeader';
import ProfessionalHero from './components/ProfessionalHero';
import ProfessionalFeatures from './components/ProfessionalFeatures';
import ProfessionalStats from './components/ProfessionalStats';
import ProfessionalAbout from './components/ProfessionalAbout';
import ProfessionalContact from './components/ProfessionalContact';
import ProfessionalFooter from './components/ProfessionalFooter';
import ProfessionalRelazione from './components/ProfessionalRelazione';

const App = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [initialized, setInitialized] = useState(false);
  const [currentSection, setCurrentSection] = useState(0);
  const [footerVisible, setFooterVisible] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
  const [isTransitioning, setIsTransitioning] = useState(false);
  const mainContainerRef = useRef(null);
  const footerRef = useRef(null);
  const sectionRefs = useRef([]);

  const sections = [
    { id: 'home', component: <ProfessionalHero /> },
    { id: 'features', component: <ProfessionalFeatures /> },
    { id: 'about', component: <ProfessionalAbout /> },
    { id: 'stats', component: <ProfessionalStats /> },
    { id: 'contact', component: <ProfessionalContact /> },
    { id: 'relazione', component: <ProfessionalRelazione /> }
  ];

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth < 768);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Inizializzazione migliorata
  useEffect(() => {
    if (!initialized && !isMobile) {
      const hash = window.location.hash.replace('#', '');
      const index = hash ? sections.findIndex(s => s.id === hash) : 0;
      
      setTimeout(() => {
        const finalIndex = index >= 0 ? index : 0;
        console.log('Initializing with section:', finalIndex, sections[finalIndex]?.id);
        setCurrentSection(finalIndex);
        setInitialized(true);
      }, 100);
    }
  }, [initialized, isMobile, sections]);

  // Aggiorna l'hash quando cambia la sezione (solo se non in transizione)
  useEffect(() => {
    if (isMobile || !initialized || isTransitioning) return;
    
    if (sections[currentSection]) {
      const newHash = sections[currentSection].id;
      console.log('Updating hash to:', newHash, 'for section:', currentSection);
      
      // Aggiorna l'hash solo se è diverso da quello attuale
      if (window.location.hash.replace('#', '') !== newHash) {
        window.location.hash = newHash;
      }
    }
  }, [currentSection, sections, isMobile, initialized, isTransitioning]);

  // Debug logging migliorato
  useEffect(() => {
    if (initialized) {
      console.log('Current section changed:', {
        index: currentSection,
        id: sections[currentSection]?.id,
        hash: window.location.hash,
        isTransitioning
      });
    }
  }, [currentSection, isTransitioning, initialized]);

  // Funzione di navigazione completamente riscritta
  const scrollToSection = (identifier) => {
    if (isTransitioning) {
      console.log('Navigation blocked - transition in progress');
      return;
    }

    let targetIndex;
    if (typeof identifier === 'number') {
      targetIndex = identifier;
    } else {
      targetIndex = sections.findIndex(s => s.id === identifier);
    }

    console.log('Navigation request:', {
      identifier,
      targetIndex,
      currentSection,
      sectionsLength: sections.length
    });

    // Validazione dell'indice
    if (targetIndex < 0 || targetIndex >= sections.length) {
      console.warn('Invalid section index:', targetIndex);
      return;
    }

    // Se siamo già nella sezione target, non fare nulla
    if (targetIndex === currentSection && !footerVisible) {
      console.log('Already in target section:', targetIndex);
      return;
    }

    // Inizia la transizione
    setIsTransitioning(true);
    setFooterVisible(false);

    // Applica il cambio di sezione con un piccolo delay per assicurare la sincronia
    requestAnimationFrame(() => {
      requestAnimationFrame(() => {
        console.log('Executing navigation to section:', targetIndex);
        setCurrentSection(targetIndex);
        
        if (isMobile) {
          const el = sectionRefs.current[targetIndex];
          if (el) {
            el.scrollIntoView({ behavior: 'smooth', block: 'start' });
          }
        }

        // Fine della transizione dopo l'animazione CSS
        setTimeout(() => {
          setIsTransitioning(false);
          console.log('Navigation completed to section:', targetIndex);
        }, 700); // Durata della transizione CSS
      });
    });
  };

  // Gestione delle frecce di navigazione con protezione anti-double-click
  const navigateNext = () => {
    if (isTransitioning || footerVisible || currentSection >= sections.length - 1) {
      return;
    }
    console.log('Navigating next from:', currentSection, 'to:', currentSection + 1);
    scrollToSection(currentSection + 1);
  };

  const navigatePrev = () => {
    if (isTransitioning || footerVisible || currentSection <= 0) {
      return;
    }
    console.log('Navigating prev from:', currentSection, 'to:', currentSection - 1);
    scrollToSection(currentSection - 1);
  };

  // Add keyboard navigation
  useEffect(() => {
    const handleKeyDown = (e) => {
      // Skip if mobile, transitioning, or footer is visible
      if (isMobile || isTransitioning || footerVisible) return;
      
      switch (e.key) {
        case 'ArrowRight':
        case 'ArrowDown':
          navigateNext();
          break;
        case 'ArrowLeft':
        case 'ArrowUp':
          navigatePrev();
          break;
        default:
          break;
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [currentSection, isTransitioning, footerVisible, isMobile]);

  // Aggiungi questo effetto per ascoltare l'evento personalizzato
  useEffect(() => {
    const handleScrollToSection = (e) => {
      if (e.detail) {
        scrollToSection(e.detail);
      }
    };

    window.addEventListener('scrollToSection', handleScrollToSection);
    return () => window.removeEventListener('scrollToSection', handleScrollToSection);
  }, []);

  if (isLoading) {
    return <ProfessionalLoader onComplete={() => setIsLoading(false)} />;
  }

  return (
    <div className="relative bg-slate-900 text-white font-sans antialiased">
      <ProfessionalParticleSystem />
      <ProfessionalHeader 
        currentSection={currentSection}
        navigateToSection={scrollToSection}
        sections={sections}
      />
      
      {/* Main content */}
      {!isMobile ? (
        <div 
          ref={mainContainerRef}
          className="flex w-full h-screen transition-transform duration-700 ease-in-out md:flex-row flex-col"
          style={{ 
            transform: initialized ? `translateX(-${currentSection * 100}vw)` : 'none',
            height: '100vh',
            opacity: initialized ? 1 : 0,
            transition: 'transform 700ms ease-in-out, opacity 300ms ease-in-out',
            pointerEvents: isTransitioning ? 'none' : 'auto' // Disabilita gli eventi durante la transizione
          }}
        >
          {sections.map((section, index) => (
            <div 
              key={section.id} 
              ref={el => sectionRefs.current[index] = el}
              className="w-screen flex-shrink-0 h-screen min-h-[100dvh] overflow-y-scroll"
              style={{
                minHeight: '100dvh',
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                overflowY: 'scroll',
                overflowX: 'hidden'
              }}
            >
              <div className="flex-1 w-full">
                {section.component}
              </div>
              {index === sections.length - 1 && (
                <div className="w-full bg-slate-900">
                  <ProfessionalFooter 
                    setFooterVisible={setFooterVisible}
                    setCurrentSection={setCurrentSection}
                  />
                </div>
              )}
            </div>
          ))}
        </div>
      ) : (
        // MOBILE: tutte le sezioni in verticale
        <div className="flex flex-col w-full min-h-screen">
          {sections.map((section, index) => (
            <div
              key={section.id}
              ref={el => sectionRefs.current[index] = el}
              className="w-full min-h-[100dvh] max-h-none overflow-y-auto"
              style={{ minHeight: '100dvh', maxHeight: 'none', overflowY: 'auto' }}
            >
              {section.component}
            </div>
          ))}
          <div className="w-full bg-slate-900">
            <ProfessionalFooter 
              setFooterVisible={setFooterVisible}
              setCurrentSection={setCurrentSection}
            />
          </div>
        </div>
      )}

      {/* Footer section solo desktop */}
      {!isMobile && (
        <div 
          ref={footerRef}
          className={`w-full min-h-screen bg-slate-900 transition-all duration-700 ${
            footerVisible ? 'block' : 'hidden'
          }`}
          style={{
            position: 'relative',
            minHeight: '100vh',
          }}
        >
          <ProfessionalFooter 
            setFooterVisible={setFooterVisible}
            setCurrentSection={setCurrentSection}
          />
        </div>
      )}

      {/* Navigation Dots e Arrows solo desktop */}
      {!isMobile && (
        <>
          <div className="fixed bottom-8 left-1/2 transform -translate-x-1/2 flex space-x-3 z-50">
            {sections.map((section, index) => (
              <button
                key={section.id}
                onClick={() => scrollToSection(index)}
                disabled={isTransitioning}
                className={`w-3 h-3 rounded-full transition-all duration-300 ${
                  currentSection === index && !footerVisible
                    ? 'bg-blue-500 scale-125'
                    : 'bg-slate-600 hover:bg-slate-400'
                } ${isTransitioning ? 'opacity-50 cursor-not-allowed' : ''}`}
                title={section.id}
              />
            ))}
          </div>
          
          {/* Left Arrow */}
          <div className="fixed top-1/2 left-4 transform -translate-y-1/2 z-50">
            <button
              onClick={navigatePrev}
              disabled={isTransitioning || footerVisible || currentSection === 0}
              className={`p-3 rounded-full backdrop-blur-lg transition-all duration-300 ${
                !footerVisible && currentSection > 0 && !isTransitioning
                  ? 'bg-slate-800/50 text-white hover:bg-slate-700/70'
                  : 'bg-slate-800/20 text-slate-600 cursor-not-allowed'
              }`}
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
            </button>
          </div>
          
          {/* Right Arrow */}
          <div className="fixed top-1/2 right-4 transform -translate-y-1/2 z-50">
            <button
              onClick={navigateNext}
              disabled={isTransitioning || footerVisible || currentSection === sections.length - 1}
              className={`p-3 rounded-full backdrop-blur-lg transition-all duration-300 ${
                !footerVisible && currentSection < sections.length - 1 && !isTransitioning
                  ? 'bg-slate-800/50 text-white hover:bg-slate-700/70'
                  : 'bg-slate-800/20 text-slate-600 cursor-not-allowed'
              }`}
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
              </svg>
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default App;

// Global styles
const styles = `
  /* Scrollbar personalizzata */
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  
  ::-webkit-scrollbar-track {
    background: rgba(30, 41, 59, 0.3);
  }
  
  ::-webkit-scrollbar-thumb {
    background: rgba(59, 130, 246, 0.5);
    border-radius: 4px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: rgba(59, 130, 246, 0.8);
  }

  /* Contenuto scrollabile */
  .scroll-section {
    height: auto;
    min-height: 100dvh;
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
  }

  .scroll-content {
    width: 100%;
    min-height: 100%;
    position: relative;
  }
`;

// Inject styles
const styleSheet = document.createElement("style");
styleSheet.type = "text/css";
styleSheet.innerText = styles;
document.head.appendChild(styleSheet);